import React from 'react';
import { Switch, Route, Link, NavLink, Redirect } from "react-router-dom";

import './App.css';

import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';

import Home from './views/Home/Home';
import About from './views/About/About';
import Dish from './views/Dish/Dish';
import List from './views/Dish/List';
import Info from './views/Dish/Info';

import NotFound from './views/NotFound/NotFound';

function App() {
  return (
    <div className="App">

      {/* <Home/> */}

      {/* <Link to="/">首页</Link>
      <Link to="/about">关于</Link> */}

      <Header />
      <Switch>
        {/* <Route path="/" exact component={Home} ></Route> */}
        <Route path="/" exact render={() => {
          return <Home data={{ name: 666 }} />
        }}></Route>
        <Route path="/about" component={About} ></Route>

        {/* <Route path="/dish" component={Dish}></Route> */}

        <Route path="/dish" render={(props) =>
          <Dish {...props} >
            <Route path="/dish" exact component={List}></Route>
            <Route path="/dish/info/:id" component={Info}></Route>
          </Dish>
        }></Route>

        {/* <Route path="/info/:id" component={Info}>
        <Route path="/info/:id" render={(props) => {
          return <Info {...props} />
        }}></Route> */}

        <Route path="*" render={(h, l, m) => {
          return <NotFound />
          return <Redirect to="/" />
        }}></Route>

      </Switch>
      <Footer />

    </div>
  );
}


export default App;
